@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0px;
  color: white;
}

.container {
  margin: 0;
  padding: 0 5px;
  max-width: 1624px;
  //border: 1px solid red;
  @media screen and (max-width: 850px) {
    overflow-x: hidden;

  }
}

.main__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 20px;
  min-height: 100vh;
  //overflow-x: hidden;
  background-color: #000000;
  @media screen and (max-width: 850px) {
    overflow-x: hidden;
  }

  &::before {
    height: 1200px;
    content: "";
    background-image: url('../../assets/Looper BG.png');
    background-size: 120%;
    background-repeat: no-repeat;
    position: absolute;
    top: -45vh;
    right: 75vw;
    bottom: 0vh;
    left: -30vw;
    overflow: visible;

    @media screen and (max-width: 1360px) {
      height: 600px;
      background-size: 120%;
      top: -25vh;
      right: 75vw;
      bottom: 0vh;
      left: -35vw;
    }

    @media screen and (max-width: 850px) {
      top: -60vh;
      right: 0vw;
      bottom: 0vh;
      left: 50px;
    }

    @media screen and (max-width: 470px) {
      top: -50vh;
      right: 0vw;
      bottom: 0vh;
      left: -250px;
    }
  }

}


