.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popup-content {
  background: #1e1e1e;
  padding: 20px;
  border-radius: 12px;
  max-width: 600px;
  width: 90%;
  color: white;
  line-height: 1.6;
  position: relative;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.6);
  animation: popupFadeIn 0.3s ease-out;
}

.popup-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.popup-close:hover {
  transform: scale(1.2);
}

@keyframes popupFadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
