.life-code-result-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 800px;
  height: 1000px;
  margin-top: 70px;
  margin-bottom: 30px;
  padding: 30px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.33);
  box-shadow: rgba(255, 255, 255, 0.33) 0px 0px 15px;

  @media screen and (max-width: 850px) {
    width: 380px;
    height: 1330px;
  }

  h3 {
    margin-bottom: 15px;
    font-family: "Shantell Sans", serif;
    font-weight: normal;
    font-size: 27px;
    color: #FFDA80;
  }

  span {
    margin-top: 25px;
    background: linear-gradient(to right, #fc72ff, #8f68ff, #487bff, #8f68ff, #fc72ff);
    background-size: 200%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: animate-gradient 2.5s linear infinite;
    font-size: 20px;

  }
  .span_hover:hover {
    cursor: pointer;
  }

  .life-code-result_top-row {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: 20px;

    @media screen and (max-width: 850px) {
      flex-direction: column;
    }

    .life-code-result_top-row_tasks {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      width: 50%;
      padding-left: 20px;

      @media screen and (max-width: 850px) {
        align-items: center;
        width: 100%;
        padding-left: 0px;
        margin-bottom: 30px;
      }

      .tasks_task {
        margin-bottom: 3px;
      }
    }

    .life-code-result_top-row_contours {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      width: 50%;
      padding-right: 20px;

      @media screen and (max-width: 850px) {
        align-items: center;
        width: 100%;
        padding-right: 0px;
        margin-bottom: 30px;
      }

      .contours_contour {
        margin-bottom: 3px;
      }
    }

  }


  .life-code-result_middle-row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    margin-top: 40px;
    @media screen and (max-width: 850px) {
      flex-direction: column;
      align-items: center;
    }

    .life-code-result_middle-row_life-code {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      width: 50%;
      padding-left: 20px;

      @media screen and (max-width: 850px) {
        align-items: center;
        width: 100%;
        padding-left: 0px;
        margin-bottom: 30px;
      }

      .life-code {
        margin-bottom: 3px;
      }
    }

    .life-code-result_middle-row_life-periods {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      width: 50%;
      padding-right: 20px;

      @media screen and (max-width: 850px) {
        align-items: center;
        width: 100%;
        padding-right: 0px;
        margin-bottom: 30px;
      }

      .life-periods_table {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 20px;
        color: white;

        .life-periods_table_row {
          //border: 1px solid red;
          display: flex;
          flex-direction: column;
          align-items: center;

          .table_row_header {
            margin-bottom: 10px;
            margin-top: 10px;

          }
        }
      }
    }
  }


  .life-code-result_bottom-row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 20px;

    .life-code-result_bottom-row_table {
      .life-table {
        width: 100%;
        border-collapse: collapse;
        text-align: center;
        color: white;

        thead {
          background: #222;
          font-weight: bold;
          font-size: 1.1rem;
        }

        th, td {
          padding: 10px 14px;
          @media screen and (max-width: 850px) {
            padding: 10px 7px;
          }
        }

        tbody tr:nth-child(odd) {
          background: rgba(255, 255, 255, 0.05); // легке чергування рядків
        }

        tbody tr:nth-child(even) {
          background: rgba(255, 255, 255, 0.1);
        }

        tbody td {
          transition: background 0.3s;
        }

        tbody td:hover {
          background: rgba(255, 255, 255, 0.2); // підсвітка при наведенні
        }
      }


    }
  }
}


