.form {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  width: 800px;
  height: 100px;
  padding: 10px 35px;
  margin-top: 15vh;
  border: 1px solid rgba(255, 255, 255, 0.33);
  box-shadow: rgba(255, 255, 255, 0.33) 0px 0px 15px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.04);
  @media screen and (max-width: 850px) {
    flex-direction: column;
    width: 380px;
    height: 240px;
    margin-top: 5vh;
    padding: 30px 25px;
  }
}

.label {
  font-family:  sans-serif;
  font-weight: bold;
  color: white;
}

.MuiInputBase-input {
  background: black;
  border: 1px solid white;
}

.MuiTextField-root {
  background: black;
  border-radius: 5px;
}

#form_btn {
  width: 150px;
  height: 50px;
  background: linear-gradient(to right,  #487bff, #fc72ff);
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 25px;
  //background: radial-gradient(circle,rgba(246,239,219,1)  0%, rgba(204,172,126,1) 100%);
  //box-shadow: 0px 3px 10px rgba(0, 0, 0, .25);
  &:hover {
    cursor: pointer;
    background: #5D6EFF;

  }
}

@media screen and (max-width: 470px) {
  .form {
    .form_h1 {
      font-size: 23px;
      text-align: center;
    }

    .form_h2 {
      font-size: 20px;
    }
  }

}